import { Component } from '@angular/core';
import { NFC } from '@ionic-native/nfc';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

/**
 * Generated class for the ToastModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-toast-modal',
  templateUrl: 'toast-modal.html',
})
export class ToastModalPage {

  timeOut;
  message;
  action;
  icon;

  constructor(
  	public navCtrl: NavController, 
  	public navParams: NavParams,
  	public viewCtrl: ViewController,
    private nfc: NFC
  ) {
  	const option:any = this.navParams.get('option');
  	this.message = option.message ? option.message : '';
  	this.icon = option.icon;
  	if(option.duration) this.timeOut = option.duration;
  	if(option.action) this.action = option.action;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ToastModalPage');
  }

  nfcSetting() {
    this.viewCtrl.dismiss();
    this.nfc.showSettings();
  }

  back(){
  	this.viewCtrl.dismiss();
  }
}



